import React from "react";
import { Link } from "react-router-dom";
import ojalaquede from "../ojalaquede.PNG";
import mujer from "../mujer.PNG";

function Home() {
  return (
    <div>
      <br />

      <div class="caja">
        <div class="row">
          <div class="col">
            <p class="titulo">Bienvenido!</p>
          </div>
        </div>

        <div class="row">
          <div class="col">
            <p class="subtitulo">
              Antes de comenzar conoce los tipos de patentes que existen, el
              proceso es el mismo para ambos. Sin embargo, es importante escoger
              el tipo de patente que se adapte mejor a tu idea y seleccionarlo
              al momento de llenar el formulario.
            </p>
            <br />
          </div>
        </div>
        <hr />

        <div class="row">
          <div class="col-5">
            <br />
            <br />
            <br />
            <br />
            <br />
            <img
              src={ojalaquede}
              class="img-responsive"
              alt="Responsive"
              width="400"
              height="300"
            />
            <br />
            <br />
          </div>
          <div class="col subtitulo2">
            <p class="titulo">Patente de modelo de utilidad</p>
            <p>
              Los modelos de utilidad se consideran particularmente adaptados
              para las PYME que efectan mejoras "menores" en productos
              existentes o adapten dichos productos. Los modelos de utilidad se
              utilizan principalmente para las innovaciones mecnicas.
            </p>
            <p>
              El modelo de utilidad slo contempla la proteccin de invenciones
              de producto; en cambio en la patente de invencin se protegen
              invenciones de producto y, tambin, de procedimiento. La invencin
              protegida en el modelo de utilidad debe ser nueva y tener
              aplicacin industrial. El perodo de proteccin del modelo de
              utilidad es de 10 aos.
            </p>
            <p>Requiere:</p>
            <ul>
              <li>Novedad:</li>
              <p>
                que una invencin es nueva cuando no est comprendida en el
                estado de la tcnica.
              </p>
              <li>Aplicacin industrial:</li>
              <p>
                Significa que la invencin puede ser fabricada o utilizada en
                cualquier tipo de industria.
              </p>
            </ul>
            <br />
            <br />
          </div>
        </div>
        <hr />
        <div class="row">
          <div class="col-5">
            <br />
            <br />
            <br />
            <br />
            <br />
            <img
              src={mujer}
              class="img-responsive"
              alt="Responsive"
              width="400"
              height="300"
            />
            <br />
            <br />
          </div>
          <div class="col subtitulo2">
            <p class="titulo">Patente de invencin</p>
            <p>
              La Patente es un privilegio que le otorga el Estado al inventor
              como reconocimiento de la inversin y esfuerzos realizados por
              ste para lograr una solucin tcnica que le aporte beneficios a
              la humanidad. Dicho privilegio consiste en el derecho a explotar
              exclusivamente el invento por un tiempo determinado.
            </p>
            <p>
              {" "}
              durante 20 aos puede explotar el invento. La explotacin puede
              consistir en comercializar exclusiva y directamente el producto
              patentado, o por intermedio de terceros otorgando licencias, o
              transfiriendo los derechos obtenidos mediante su venta para que un
              tercero explote la invencin. En conclusin, el beneficio es
              econmico para el inventor o titular de la patente
            </p>
            <p>Requiere:</p>
            <ul>
              <li>Novedad:</li>
              <p>
                que una invencin es nueva cuando no est comprendida en el
                estado de la tcnica.
              </p>
              <li>Nivel inventivo:</li>
              <p>
                Significa que la invencin no debe deducirse del estado de la
                tcnica de forma obvia o evidente para un experto en la materia.
              </p>
              <li>Aplicacin industrial:</li>
              <p>
                Significa que la invencin puede ser fabricada o utilizada en
                cualquier tipo de industria.
              </p>
            </ul>
            <br />
            <br />
          </div>
        </div>

        <div class="row">
          <div class="col boton">
            <Link to={"/paso1"}>
              <button className="btn btn-primary">Comenzar</button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
